.mainContainer
{
    display: flex;
    flex-direction: row;
    justify-content: center;
    /* border: 2px solid salmon; */
    /* width: 100%; */
    height: 95vh;
    width: 100vw;
}

.mobileSelectButtonLeft
{
    display: none;
}

.mobileSelectButtonRight
{
    display: none;
}

.mainText
{
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    color: white;
    font-size: 14vh;
    text-align: center;
    /* border: 2px solid blue; */
    height: 50%;
    width: 70%;
    align-self: center;
    margin-top: 20%;
}

.mainBody
{
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
}

.logoDiv
{
    /* border: 2px solid bisque; */
}

.tagLogo
{
    width: 40%;
}

.selectButtonLeft
{
    background-color: transparent;
    border: none;
    /* border: 2px solid green; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: fit-content;
}

.selectButtonLeft:hover
{
    cursor: pointer;
    
}

.selectButtonRight
{
    background-color: transparent;
    border: none;
    /* border: 2px solid green; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: fit-content;
}

.selectButtonRight:hover
{
    cursor: pointer;
    
}


.buttonImageLeft
{
    width: 307px;
    height: 339px;
    object-fit: none;
    /* border: 2px solid purple; */
    transition: 1s;
    /* border: 2px solid salmon; */
    /* height: 66px; */
}

.buttonImageLeft:hover
{
    /* zoom: 150%; */
    /* background-color: aqua; */
    transform: scale(1.2);
    margin-left: 2.5vw;
    /* margin-right: 30%; */
}

.buttonImageRight
{
    width: 307px;
    height: 339px;
    /* border: 2px solid purple; */
    transition: 1s;
    /* border: 2px solid salmon; */
    /* height: 66px; */
}

.buttonImageRight:hover
{
    /* zoom: 150%; */
    /* background-color: aqua; */
    transform: scale(1.2);
    /* margin-left: 15%; */
    margin-right: 2.5vw;
}

.bruhmoment
{
    color: white;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;

    font-size: 3vh;
    /* border: 2px solid red; */
    /* justify-self: end; */
    /* position: fixed; */
    margin-top: 40%;
    /* bottom: 20px; */
}


/* RESPONSIVENESS */
@media only screen and (max-width: 500px)
{
    .mobileSelectButtonLeft
    {
        color: white;
        font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
        display: flex;
        flex-direction: column;
        visibility: visible;
        justify-self: center;
        margin-top: 20%;
        /* border: 2px solid greenyellow; */
    }

    .selectButtonLeft
    {
        display: none;
    }

    .mobileSelectButtonRight
    {
        color: white;
        font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
        display: flex;
        flex-direction: column;
        visibility: visible;
        justify-self: center;
        margin-top: 20%;
        /* border: 2px solid greenyellow; */
    }

    .selectButtonRight
    {
        display: none;
    }

    .mainText
    {
        font-size: 3.5vh;
        /* border: 2px solid red; */
        margin-top: 150%;
        text-align: center;
    }
    
    /* .mobileHeading
    {
        margin-top: 250%;
    } */

    .buttonImageLeft
    {
        margin-top: 150%;
        width: 100px;
        height: 100px;
        object-fit: contain;
    }

    .buttonImageRight
    {
        margin-top: 150%;
        width: 100px;
        height: 100px;
        object-fit: contain;
    }

    .logoDiv
    {
        margin-top: -10%;
    }
}